footer {
    background: $black;
    color: $lightgrey;
    @media(max-width: 991px){
        padding-top: 0;
    }

}
.footer {
    &-logo {
        margin-top: 30px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-bottom: 40px;

        & svg {
            fill: $lightgrey;
            width: 100%;
            max-height: 40px;
            margin-top: 14px;
            max-width: 200px;
        }

        @media(max-width:1200px) {
            margin-bottom: 30px;
        }

        &-path {

            &-1,
            &-3 {
                fill: $white;
            }

            &-2 {
                fill: $red;
            }

        }
    }

    &-contact {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        flex-flow: row wrap;
        margin-bottom: 23px;
        margin-bottom: 5px;

        & a {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            font-size: 18px;
        }

        & svg {
            width: 20px;
            height: 34px;
            margin-right: 10px;
        }

        & a,
        & svg {
            color: $lightgrey;
            fill: $lightgrey;

            &:hover {
                color: $white;
                fill: $white;
            }

        }
    }

    &-social {
        display: flex;
        flex-direction: column;
        margin: 0;
        list-style: none;
        margin-bottom: 20px;

        &:first-child{
            margin-right: 60px;

            @media(max-width: 1200px){
                margin-right: 0;
            }

            @media(min-width: 390px) and (max-width: 992px){
                margin-right: 50px;
            }
            @media(max-width: 390px){
                margin-right: 0;
            }
        }
        &:last-child{
            @media(max-width: 390px){
                margin-left: 10px;
            }
        }

        

        &-wrapper{
            display: flex;
            align-items: flex-end;
            justify-content: flex-end;
            width: 100%;
            flex-direction: row;

            margin-top: 50px;
            &.collapse{
                @media(min-width: 992px){
                    display: flex!important;
                }
            }

            @media(min-width: 992px) and (max-width: 1200px){
                flex-direction: column;
            }

            @media(max-width:992px){
                justify-content: flex-end;
                margin-top: 0px;
            }

            @media(max-width:470px){
                flex-flow: row wrap;
                align-items: flex-end;
            }
        }

        &__item {
            display: flex;
            justify-content: flex-end;
            align-items: center;

            &:first-child {
                .footer-social__item-icon {
                    border-top: 1px solid white;
                }
            }
            &:last-child {
                .footer-social__item-icon {
                    border-bottom: 1px solid white;
                }
            }

            & a {
                display: flex;
                justify-content: flex-end;
                align-items: center;

                &:hover {
                    & .footer-social__item-icon {
                        background: $white;

                        & svg {
                            fill: $black;
                        }
                    }
                }
            }

            &--chat {
                cursor: pointer;
                padding-top: 2em;
                padding-bottom: 2em;
                width: 200px;
                margin-left: auto;
            }

            &-text {
                display: flex;
                flex-direction: column;
                align-items: flex-end;
                padding-right: 1em;
                font-size: 12px;
                font-weight: 400;
                text-transform: uppercase;
                color: #cfcfcf;

                & span {
                    font-size: 14px;
                    font-weight: 300;
                }

                &:hover {
                    color:$white;

                }
            }

            &-icon {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 40px;
                height: 40px;
                border: 1px solid #fff;
                background-color: #000;
                background-position: center center;
                background-repeat: no-repeat;
                transition: 0.4s ease-in-out;
                filter: grayscale(0);
                border-top: none;
                border-bottom: none;

                & svg {
                    fill: $white;
                    width: 17px;
                    height: 17px;
                }
            }

        }

    }


    &-nav {
        width: 100%;
        position: relative;
        margin-top: 10px;
        margin-bottom: 80px;
        transition: all .5s ease-in-out;
        display: flex;
        flex-direction: column;

        @media(max-width: 993px){
            margin-bottom: 10px;
            margin-top: 10px;
        }

        &.collapse{
                    
            @media(min-width: 992px){
                display: flex!important;
            }
        }

        &__item {
            list-style: none;
            padding: 5px 10px;
            font-size: 14px;

            @media(max-width: 991px){
                padding: 2px 10px;
                margin-left: 10px;
                
            }

            &-head {
                text-transform: uppercase;
                color: $white;
                font-size: 18px;
                font-weight: 400;
                 margin-bottom: 15px;
                letter-spacing: 0.03rem;
                background: none;
                border: none;
                width: 100%;
                position: relative;
                text-align: left;
                padding: 10px;
                transition: all .3s ease-in-out;
                margin-top: 50px;

                @media(max-width: 991px){
                    margin-top: 10px;
                    font-size: 15px;
                    margin-bottom: 10px;
                }

                @media(min-width: 992px){
                    pointer-events: none;
                }


                &[aria-expanded='true']{

                    & .vertical-line{
                        transition: all .3s ease-in-out;
                        opacity: 0;
                    }

                    &:before{
                        opacity: 0;
                        transition: all .3s ease-in-out;
                    }

                }

                & svg{
                    fill: $white;
                    width: 15px;
                    height: 15px;
                    position: absolute;
                    right: 10px;
                    top: 50%;
                    transform: translateY(-50%);
                    display: none;

                    @media(max-width: 991px){
                        display: flex;
                    }
                }

                &:before{
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 1px;
                    background: $border-color;
                    opacity: .3;
                    display: none;
                    transition: all .3s ease-in-out;

                    @media(max-width: 991px){
                        display: flex;
                    }
                }
            }

            & a,
            & svg {
                color: $VIPlightgrey;
                fill: $lightgrey;

                &:hover {
                    color: $white;
                    fill: $white;
                }

            }
        }
    }
}
